html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.books-list {
  padding: 0px;
  list-style: none;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: flex-start;
}

.book {
  margin: 30px;
  width: 130px;
}

.book-top {
  width: 128px;
  height: 193px;
  padding: 0px;
  position: relative;
  box-shadow: 0px 2px 11px 0px rgba(0,0,0,0.66);
}

.book-cover {
  width: 100%;
  height: 100%;
  background: #eee;
}

.book-shelf-changer {
  position: absolute;
  right: -10px;
  bottom: -10px;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: #79b6d6;
  background-image: url(/my-reads-map/static/media/arrow-drop-down.9d4e3f36.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: 20px;
  box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
}

.book-shelf-changer:hover {
  background-color: #4682b4;
}

.book-shelf-changer select {
  width: 100%;
  height: 100%;
  opacity: 0;
  cursor: pointer;
}

.book-title,
.book-authors {
  font-size: 0.8em;
  text-align: center;
}

.book-title {
  margin-top: 10px;
}

.book-authors {
  color: #999;
}

@media screen and (max-width: 690px) {
  .books-list {
    justify-content: center;
  }
}
.compartment {
  padding: 30px;
  text-align: left;
}

.compartment > h2 {
  display: block;
  line-height: 50px;
  border-bottom: 1px solid gray;
}

@media screen and (max-width: 690px) {
  .compartment {
    text-align: center;
  }
}
header {
  width: 100%;
  background-color: #20232a;
  padding: 10px;
}

header h1 {
  margin: 0px;
  color: #79b6d6;
  display: inline;
  cursor: pointer;
}

header h1:hover {
  color: #61dafb;
}

.open-search {
  position: fixed;
  right: 25px;
  bottom: 25px;
}

.open-search .add-btn {
  display: block;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: #4682b4;
  background-image: url(/my-reads-map/static/media/add.75d390e5.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: 28px;
  box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
  font-size: 0;
  cursor: pointer;
}

.open-search .map-btn {
  margin-bottom: 10px;
  display: block;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: #4682b4;
  background-image: url(data:image/png;base64,);
  background-repeat: no-repeat;
  background-position: center;
  background-size: 28px;
  box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
  font-size: 0;
  cursor: pointer;
}

.open-search a:hover {
  background-color: #2a5d86;
}
.search-page {
  width: 100%;
  margin: 0px;
  padding: 0px;
}

.search-bar {
  width: 100%;
  padding: 10px;
  display: flex;
  flex-direction: row;
  box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 0 6px rgba(0,0,0,0.23);
}

.search-bar input {
  flex: 1 1;
  padding: 15px 10px;
  font-size: 1.25em;
  border: none;
  outline: none;
}

.close-search {
  width: 50px;
  background: white;
  background-image: url(/my-reads-map/static/media/arrow-back.20e8847d.svg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: 28px;
}
.App {
  text-align: center;
  position: relative;
}

@media screen and (min-width: 1500px) {
  .App {
    width: 1400px;
    margin: 0 auto;
  }
}
aside {
  width: 300px;
  overflow-y: hidden;
  background-color: rgb(0, 0, 0, 0.77);
  outline: 1px solid #000000;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  left: 0;
  bottom: 0;
  z-index: 4;
  -webkit-transition: height linear 100ms;
  transition: height linear 100ms;
  box-shadow: 0px 2px 15px 0px rgba(0,0,0,0.75);
  text-align: left;
}

.full-height {
  height: 100vh;
}

.min-height {
  height: 40px;
}

aside input {
  width: 100%;
  height: 50px;
  padding: 15px;
  font-size: 22px;
  outline: 1px solid black;
  border: none;
  background-color: #3e444e;
  color: white;
}

aside input:hover, aside input:focus {
  background-color: #555d69;
}

.container {
  width: 100%;
  overflow: hidden;
}

.container:hover {
  overflow: auto;
}

.hide {
  height: 0px;
}

.show {
  height: auto;
}


ul {
  margin: 10px 0px;
  list-style: none;
  font-size: 20px;
  overflow: auto;
  padding: 10px;
}

li {
  padding: 10px;
}

li span {
  outline: none;
  cursor: pointer;
}

li .none {
  color: rgb(177, 177, 177);
}

li .highlight {
  color: white;
  text-decoration: underline;
}

li .none:hover {
  color: rgb(223, 222, 222);
}

.toggle {
  width: 100%;
  flex: 0 0 40px;
  background-color:#282c34;
  color: white;
  border: none;
}

.toggle:hover, .toggle:focus {
  background-color: #3e444e; 
}

.toggle .icon {
  font-size: 40px;
}

div {
  margin: 0px;
}

@media screen and (max-width: 600px) {
  aside {
    width: 100%;
    
  }
}
.info-window {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  justify-content: center;
  width: 200px;
  height: 200px;
  margin: 0px;
  font-size: 14px;
  color: black;
  font-weight: 400;
}

.info-window img {
  height: 100px;
  margin: 10px 0px;
  border-radius: 2px;
}

small {
  font-size: 12px;
}
#map {
  width: 100vw;
  height: 100vh;
}
.modal {
  position: absolute;
  top: 30vh;
  left: 0;
  right: 0;
  margin: 0 auto;
  width: 300px;
  z-index: 5;
  padding-bottom: 20px;
  border-radius: 10px;
  background-color: #3e444e;
  box-shadow: 0px 2px 16px 0px rgba(0,0,0,0.73);
  text-align: center;
  color: honeydew;
}

.err-head {
  font-size: 30px;
}

.err-meg {
  font-size: 20px;
}

.err-btn {
  height: 30px;
  background-color: honeydew;
  color: #3e444e;
  font-weight: bold;
}
.App {
  text-align: center;
}

main {
  position: relative;
  display: flex;
  flex-direction: row;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
